<script>
import axios from 'axios';
export default {
  name: 'home',
  props: [ 'lang' ],
  data() {
    return{
      url: this.$parent.url,
      ready: false,
      strings: {}
    }
  },
  watch:{
    lang: function(newLang, oldLang){
      this.setLang();
    }
  },
  created: function(){
    this.setLang();
  },
  methods: {
    setLang : function(){
      axios.get(this.url+'/assets/lang/'+this.lang+'/home.json')
      .then(r=> {
        this.strings = r.data;
        this.ready = true;
      })
    }
  }
}
</script>
