<script>
import axios from 'axios';

export default {
  name: 'App',
  data: () => ({
    //return{
      lang : 'ar',
      url: '',
      ready: false,
      strings: {}
    //}
  } ),
  created: function(){
    this.setLang();
  },
  watch:{
    lang: function(newLang, oldLang){
      this.setLang();
    }
  },
  methods: {
    setLang : function(){
      axios.get(this.url+'/assets/lang/'+this.lang+'/main.json')
      .then(r=> {
        this.strings = r.data;
        this.ready = true;
      })
    }
  }
}
</script>


